import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {RouterModule, Router } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { ContactComponent } from './contact/contact.component';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import {SafePipe } from 'src/app/SafePipe';
import { PariculatContactsComponent } from './pariculat-contacts/pariculat-contacts.component';

@NgModule({
  declarations: [ContactComponent,SafePipe, PariculatContactsComponent],
  imports: [
    CommonModule,
    FormsModule,
    Ng2SearchPipeModule,
    RouterModule.forChild([
      {path:'Dashboard',component:ContactComponent},
      {path:'Contact/:ContactId',component:PariculatContactsComponent}
    ])
  ]
})
export class DashboardModule { }
