import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpErrorResponse, HttpParams } from '@angular/common/http';
import * as io from 'socket.io-client';
import { Observable, from } from 'rxjs';
import { tap } from 'rxjs/operators';
import { map, filter, catchError, mergeMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SocketContactService {
   
  private baseUrl = "http://localhost:3002";
  private socket;
  private ContactUrl = "http://localhost:3002/api/v1/contacts";

  constructor(public _http: HttpClient) {
    this.socket = io.connect(this.baseUrl, { transports: ['websocket'], secure : true });
  }

  public import_contacts = (import_contacts_data) => {
    this.socket.emit("import-contacts", import_contacts_data);
    this.socket.emit("unique_contact_id",import_contacts_data);
  }

  public get_all_contacts = () => {
    let response = this._http.get(`${this.ContactUrl}/view_all_contacts`);
    return response;
  }

  public update_contact_email_address = (data_to_update) => {
    this.socket.emit("update_email_address", data_to_update);
  }

  public update_contact_fname = (data_to_update) => {
    this.socket.emit("update_fname", data_to_update);
  }

  public update_contact_lname = (data_to_update) => {
    this.socket.emit("update_lname", data_to_update);
  }

  public update_home_phone = (data_to_update) => {
    this.socket.emit("update_home_phone", data_to_update);
  }

  public update_mobile_phone = (data_to_update) => {
    this.socket.emit("update_mobile_phone", data_to_update);
  }

  public get_all_contacts_sync = () => {
    return Observable.create((observer) => {
      this.socket.on('import-contacts', (data) => {
        observer.next(data);
      }); // end Socket
    }); // end Observable
  }

  public get_current_contact_info = (contactid) => {
    let params = new HttpParams()
                  .set('contactId',contactid)

    let response = this._http.post(`${this.ContactUrl}/Contact_info`,params);
    return response;
  }

}
